import React, { useState, useEffect } from "react"
import { navigate } from "gatsby"
import { handleLogin } from "../../database/functions"
import { supabase } from "../../database/supabaseClient"

export default function Login() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [message, setMessage] = useState("")

  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      if (data.session) navigate("/members/dashboard")
    })
  }, [])

  const onSuccess = () => {
    setMessage("✅ Login successful! Redirecting...")
    setTimeout(() => navigate("/members/dashboard"), 1000)
  }

  const onFail = (err?: string) => {
    setMessage("❌ " + (err || "Invalid credentials."))
  }

  return (
    <div style={{ maxWidth: 400, margin: "auto", padding: 20, display: "flex", flexDirection: "column", gap: 10, color : 'white'}}>
      <h2 style={{ textAlign: "center" }}>Login</h2>

      <form
        onSubmit={(e) => {
          e.preventDefault()
          handleLogin({ email, password, onSuccess, onFail })
        }}
        style={{ display: "flex", flexDirection: "column", gap: 10 }}
      >
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
          style={{ padding: 10, border: "1px solid #ccc", borderRadius: 5 }}
        />

        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          style={{ padding: 10, border: "1px solid #ccc", borderRadius: 5 }}
        />

        <button
          type="submit"
          style={{
            padding: 10,
            borderRadius: 5,
            background: "#1976D2",
            color: "#fff",
            border: "none",
            cursor: "pointer",
          }}
        >
          Login
        </button>
      </form>

      {message && <p style={{ textAlign: "center", marginTop: 10 }}>{message}</p>}
    </div>
  )
}
